<template>
  <div
    v-if="typeof flag === 'boolean'" 
    class="tile-round"
    :class="[{ disabled: flag === false }, svg ? `svg-${svg}` : null, color]"
    @click="() => { if (flag === true) this.$emit('clickControl') }"
  />
</template>

<script>
  export default {
    props: {
      flag: { type: [Boolean, null], default: true },
      svg: { type: String, default: null },
      color: { type: String, default: 'bg-blue' },
    }
  }
</script>