<template>
  <div
    v-if="typeof enableFlag === 'boolean'"
    class="bg-blue tile-round stopwatch"
    :class="{'svg-stopwatch': !timerFlag, disabled: !enableFlag}"
    @click="clickHandler"
  >
    <div
      v-if="false && timerFlag && timer > 0"
      class="timer"
    >{{ timer }}</div>
    <div
      v-if="false && timerFlag"
      :class="{result: timer === 0}"
      class="points"
    >{{ pointsTimer }}</div>

    <svg 
      v-if="runFlag"
      width="8vw"
      height="8vw"
      viewBox="0 0 800 800"
      style="transform:rotate(-90deg)"
    >
      <circle
        r="350"
        cx="400"
        cy="400"
        fill="transparent"
        stroke="#ffffff"
        stroke-linecap2="round"
        stroke-width="100"
        stroke-dasharray="2198"
        :stroke-dashoffset="`${Math.floor(2198/seconds*timer-2198)}`"
      />
    </svg>
    <div
      v-if="timerFlag && (timer === 0 || runFlag)"
      class="label points"
    >
      {{ pointsTimer }}
    </div>
    <div
      v-if="timerFlag && timer > 0 && !runFlag"
      class="label timer"
    >
      {{ timer }}
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      before: { type: Number, default: 3 },
      seconds: { type: Number, default: 60 },
      enableFlag: { type: [Boolean, null], default: true },
      resultFlag: { type: [Boolean, null], required: true },
    },
    emits: ['onTimerBegin', 'onTimerStart', 'onTimerFinish', 'onTimerEnd'],
    data() {
      return {
        timer: null,
        interval: null,
        intervalValue: 1000,
        tickTimestamp: null,

        timerFlag: false,
        runFlag: false,

        pointsTimer: 0,
      }
    },
    beforeUnmount() {
      this.resetTimer()
    },
    watch: {
      resultFlag: {
        handler(resultFlag) {
          if (this.runFlag) {
            // console.log('resultFlag', resultFlag)
            if (resultFlag === true) {
              this.pointsTimer += 1
            }
          }
          if (this.timerFlag && this.timer > 0) {
            if (this.resultFlag === true) this.stopTimer()
            else if (this.resultFlag === null) this.startTimer()
          }
        }
      },
    },
    methods: {
      clickHandler() {
        if (this.enableFlag) {
          if (!this.timerFlag) {
            this.onTimerBegin()
          } else {
            if (this.runFlag) {
              this.onTimerFinish()
            } else {
              if (this.timer > 0) {
                this.onTimerStart()
              } else {
                this.onTimerEnd()
              }
            }
          }
        }
      },
      onTimerBegin() {
        this.timerFlag = true
        this.$emit('onTimerBegin')
        this.before > 0 ? this.runTimer(this.before) : this.onTimerStart()
      },
      onTimerStart() {
        this.pointsTimer = 0
        this.timerFlag = true
        this.runFlag = true
        this.runTimer(this.seconds)
        this.$emit('onTimerStart')
      },
      onTimerFinish() {
        // console.log('onTimerFinish')
        this.timer = 0,
        this.runFlag = false
        this.$emit('onTimerFinish', { points: this.pointsTimer})
        this.emitter.emit('onTimerFinish', { points: this.pointsTimer})
      },
      onTimerEnd() {
        // console.log('onTimerEnd')
        this.timerFlag = false
        this.runFlag = false
        this.$emit('onTimerEnd')
      },
      onTimeIsUp() {
        this.runFlag ? this.onTimerFinish() : this.onTimerStart()
      },
      runTimer(timer) {
        // console.log('runTimer: ', timer)
        this.resetTimer()
        this.timer = timer
        this.startTimer(true)
      },
      resetTimer() {
        // console.log('resetTimer')
        this.stopTimer()
        this.intervalValue = 1000
      },
      stopTimer() {
        if (this.interval) {
          clearInterval(this.interval)   
          this.interval = null
          this.intervalValue = this.tickTimestamp ? this.intervalValue + this.tickTimestamp - Date.now() : 1000
          this.tickTimestamp = null
          // console.log('stopTimer')
        }
      },
      startTimer(init = false) {
        if (!this.interval && this.timer > 0) {
          // console.log('startTimer')
          if (init === true) {
            this.tickTimer(true)
          }
          this.tickTimestamp = Date.now()
          this.interval = setInterval(this.tickTimer, this.intervalValue)
          // console.log('intervalValue:', this.intervalValue)
        }
      },
      tickTimer(init = false) {
        if (this.timer > 0) {
          this.tickTimestamp = Date.now()

          //align timer to 1000ms
          if (this.intervalValue !== 1000) {
            clearInterval(this.interval)
            this.intervalValue = 1000
            this.interval = setInterval(this.tickTimer, this.intervalValue)
          }

          this.timer -= init ? 0 : 1
          // console.log('tickTimer: ', this.timer, this.tickTimestamp)
          if (!(this.timer > 0)) {
            this.onTimeIsUp()
          }
        }
      },
    }
  }
</script>

<style scoped lang="scss">
.stopwatch {
  display: flex;
  flex-flow: column;
  font-weight: 600;
  position: relative;
  .timer {
    position: relative;
  }

  .label {
    position: absolute;
    width: 8vw;
    height: 8vw;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4vw;
    

    &.points {
      color: $color-yellow-light;
    }

    &.timer {
      color: $color-white1;
    }
  }
}
</style>