<template>
    <TransitionGroup
      tag="div"
      name="main-transition"
      class="main area-main"
      @before-enter="el => transitionEnterHandler(el)"
    >
        <TileList
          key="result-tile-list"
          id="result-tile-list"
          v-if="hintFlag !== true && !(timerEnableFlag === true && timerRunFlag === false)"

          class="bg-blue-tiles not-disabled"
          :class="{'animation-shake': (resultFlag === false)}"
          :tileList="resultList"
          @clickTile="e => clickControl('clickTileResult', e)"
        />
        <TileList
          key="input-tile-list"
          v-if="hintFlag !== true && resultFlag !== true && mountedFlag === true && !(timerEnableFlag === true && timerRunFlag === false)"

          class="bg-yellow-tiles"
          :tileList="inputList"
          @clickTile="e => clickControl('clickTileInput', e)"
        />
      <div
        class="wrapper"
        key="hint"
        v-if="hintFlag === true"
      >
        <slot name="hint"/>
      </div>
    </TransitionGroup>

    <ControlsPanel>
      <template #controls-first>
        <ControlsPanelButton
          svg="back"
          @clickControl="clickBackHandler(backCategoryId)"
        />
        <CurrentProfile
          :timerEnableFlag="timerEnableFlag"
          :hintFlag="hintFlag"
          :resultFlag="resultFlag"
          :gameId="gameId"
          :modeId="modeId"
        />
      </template>
      <template #controls-center>
        <ControlsPanelButton
          svg="reload"
          :flag="resultFlag || timerEnableFlag ? null : hintFlag !== true"
          @clickControl="clickControl('clickControlRefresh', e)"
        />
        <StopWatch
          :enableFlag="typeof timerEnableFlag === 'boolean' && (!resultFlag || timerRunFlag) ? (hintFlag === false) : null"
          :resultFlag="resultFlag"
          @onTimerBegin="this.$emit('onTimerBegin')"
          @onTimerStart="this.$emit('onTimerStart')"
          @onTimerFinish="this.$emit('onTimerFinish')"
          @onTimerEnd="this.$emit('onTimerEnd')"
        />
        <ControlsPanelButton
          svg="lamp"
          :flag="resultFlag || timerEnableFlag ? null : typeof hintFlag === 'boolean' || null"
          @clickControl="clickHintHandler"
        />
      </template>
      <template #controls-last>
        <ControlsPanelButton
          svg="check"
          :flag="resultFlag || (timerEnableFlag === true && timerRunFlag === false) ? null : inputFlag && hintFlag !== true"
          @clickControl="clickControl('clickControlCheck', e)"
        />
        <ControlsPanelButton
          svg="play"
          :flag="resultFlag ? true : null"
          @clickControl="clickControl('clickControlNext', e)"
        />
      </template>
    </ControlsPanel>
</template>

<script>
import TileList from '@/components/TileList'
import ControlsPanel from '@/components/ControlsPanelBase'
import ControlsPanelButton from '@/components/ControlsPanelButton'
import CurrentProfile from '@/components/CurrentProfile'
import DomMixin from '@/mixins/domMixin'
import StopWatch from '@/components/StopWatch'
export default {
  mixins: [
    DomMixin,
  ],
  created() {
    this.domSetDocumentBackgroundUrl(require('/public/img/bg-game1-1080p.png'));
  },
  mounted() {
    this.mountedFlag = true;
  },
  props: ['inputFlag', 'resultFlag', 'hintFlag', 'timerEnableFlag', 'timerRunFlag', 'resultList', 'inputList', 'backCategoryId', 'gameId', 'modeId'],
  data() {
    return {
      mountedFlag: false,
    }
  },
  components: {
    ControlsPanel,
    ControlsPanelButton,
    TileList,
    CurrentProfile,
    StopWatch,
  },
  emits: [
    'clickTileResult',
    'clickTileInput',
    'clickControlRefresh',
    'clickControlNext',
    'clickControlCheck',
    'clickControlHint',
    'onTimerBegin',
    'onTimerStart',
    'onTimerFinish',
    'onTimerEnd',
  ],
  methods: {
    clickBackHandler(backCategoryId) {
      this.$router.push({ name: 'games-list', params: { categoryId: backCategoryId }});
    },
    clickHintHandler() {
      if (this.hintFlag === true) {
        this.mountedFlag = false
      }
      this.clickControl('clickControlHint')
    },
    clickControl(emit, e = null) {
      this.$emit(emit, e)
    },
    transitionEnterHandler(el) {
      if (el.id === 'result-tile-list') {
        this.mountedFlag = true
      }
    },
  },
}
</script>

<style scoped lang="scss">
.main {
    justify-content: center;
}

.main-transition-move,
.main-transition-enter-active,
.main-transition-leave-active {
  transition: all 1s ease;
  overflow: hidden;
}

.main-transition-enter-from,
.main-transition-leave-to {
  opacity: 0;
  transform: scale(0.8);
}
.main-transition-leave-active {
  position: absolute;
  left: 0;
  right: 0;
}

.animation-shake {
  animation: animation-shake 0.8s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

@keyframes animation-shake {
  10%,
  90% {
    transform: translateX(-0.25vw);
  }

  20%,
  80% {
    transform: translateX(0.5vw);
  }

  30%,
  50%,
  70% {
    transform: translateX(-1vw);
  }

  40%,
  60% {
    transform: translateX(1vw);
  }
}
</style>

<style>
.tile-list.not-disabled .tile-item.disabled, .tile-list.not-disabled .tile-item.disabled .tile-content {
  opacity: 1;
}
</style>